.card-box {
  width: 235px;
  height: -webkit-max-content;
  height: max-content;
  border: 1px solid #cbcccf;
  background-color: #fff;
  /* margin: auto; */
  margin-bottom: 10px;
  border-radius: 4px;
}
.card-box:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  transition: all 800ms;
  box-shadow: -4px 4px 15px rgba(0, 0, 0, 0.219);
}
.card-description h2 {
  font-size: 20px;
  margin-top: 1rem;
  margin-bottom: 0px;
  text-align: center;
}
.card-box .card-image {
  width: 100%;
  height: 200px;
}
.card-box .card-image img {
  height: 100%;
}
.card-inner-box {
  padding: 15px;
  display: block;
  text-decoration: none;
  color: rgb(0, 0, 0);
}
.card-inner-box:hover {
  color: #000000;
}
.ratings > span {
  font-size: 12px;
  font-weight: 500;
}
.card-description > span {
  font-size: 22px;
  text-align: center;
  display: block;
}

.single-product .product-img {
  width: 100%;
  height: 500px;
}
.product-img img {
  height: 100%;
  width: 100%;
  object-fit: contain !important;
  object-position: center !important;
}
.product-img {
