  border: 1px solid rgb(211, 206, 206);
  padding: 10px;
}
.mini-image-box-wrapper {
  position: absolute;
  left: 0%;
  top: 0%;
}
.mini-img-box {
  width: 50px;
  height: 60px;
  margin: 8px;
  border: 1px solid rgb(255, 255, 255);
}
.mini-img-box:hover {
  box-shadow: 1px -1px 5px #f3841c;
  cursor: pointer;
  border: 1px solid #f3841c;
}
.mini-img-box img {
  width: 100%;
  height: 100%;
}
.quantity-wrapper {
  display: flex;
  width: -webkit-max-content;
  width: max-content;
}
.quantity-wrapper label {
  font-weight: bold;
  margin-right: 10px;
}
.quantity-box > button,
input {
  width: 40px;
  background-color: #fff;
  border: 1px solid #b0b1b3;
  font-size: 16px;
  color: #232f3e;
  font-weight: 600;
}
.quantity-box input {
  border-left: none;
  border-right: none;
  text-align: center;
}
.quantity-box input:focus {
  outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -webkit-appearance: textfield;
          appearance: textfield;
}

.cart-button,
.check-button {
  padding: 15px 10px;
  text-align: center;
  background-color: #232f3e;
  color: #fff;
