}
.cat-dropdown {
  display: flex;
  flex-flow: column;
  position: absolute;
  background-color: var(--main-web-color);
  z-index: 1000;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  transition: all 600ms;
  box-shadow: 2px -2px 10px #ffffff5b;
  width: -webkit-max-content;
  width: max-content;
}
.cat-click::selection,
.cat-dropdown a::selection {
